.board {
  width: 100vw;
  height: 100vh;
  padding: 21px 0px 7px 0;
  touch-action: none; }
  .board .board-title {
    margin-left: 20px;
    margin-bottom: 14px;
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1.5; }
  .board .board-lists {
    display: flex;
    align-items: flex-start;
    overflow-x: auto;
    overflow-y: hidden;
    width: 100%;
    height: calc(calc(100vh - 21px - 2.7rem - 14px - 7px - 40px - 10px - 2 * 3px) + 40px + 2 * 3px + 10px);
    padding-bottom: 10px;
    padding-left: 8px;
    box-sizing: border-box;
    -webkit-user-select: none;
            user-select: none;
    scrollbar-width: thin; }
    .board .board-lists::-webkit-scrollbar {
      height: 10px; }
    .board .board-lists::-webkit-scrollbar-thumb {
      background: rgba(193, 198, 201, 0.5);
      border-radius: 9px; }
    .board .board-lists::-webkit-scrollbar-track {
      background: rgba(9, 30, 66, 0.08);
      border-radius: 9px; }
    .board .board-lists .board-list-zone, .board .board-lists .add-list-wrapper {
      flex: 0 0 auto;
      margin-left: 12px;
      margin-top: 3px; }
    .board .board-lists .board-list-zone {
      height: calc(100vh - 21px - 2.7rem - 14px - 7px - 40px - 10px - 2 * 3px); }
    .board .board-lists .placeholder {
      background-color: rgba(21, 21, 21, 0.1); }
    .board .board-lists .add-list-form {
      margin-top: 12px; }
    .board .board-lists::after {
      content: '';
      flex: 0 0 12px;
      height: 100%; }

.card-list {
  position: relative;
  width: 272px;
  border-radius: 2px;
  background-color: #e3e5ea;
  transition: -webkit-transform 0.4s cubic-bezier(0.48, 0.01, 0.36, 1.26);
  transition: transform 0.4s cubic-bezier(0.48, 0.01, 0.36, 1.26);
  transition: transform 0.4s cubic-bezier(0.48, 0.01, 0.36, 1.26), -webkit-transform 0.4s cubic-bezier(0.48, 0.01, 0.36, 1.26);
  touch-action: none; }
  .card-list:focus {
    outline: 3px solid #87ACE7; }
  .card-list.dragged {
    z-index: 1; }
  .card-list header {
    padding: 8px 14px;
    cursor: pointer; }
    .card-list header .list-title {
      color: black;
      font-family: Montserrat, "sans-serif";
      font-size: 1.3rem;
      font-weight: bold;
      line-height: 1.5;
      word-break: break-word; }
  .card-list .list-cards {
    padding: 0 14px;
    overflow-y: auto;
    overflow-x: hidden;
    max-height: calc(calc(100vh - 21px - 2.7rem - 14px - 7px - 40px - 10px - 2 * 3px) - calc(1.3rem * 1.5 + 2 * 8px) - 43px);
    scrollbar-width: thin; }
    .card-list .list-cards::-webkit-scrollbar {
      width: 9px; }
    .card-list .list-cards::-webkit-scrollbar-thumb {
      background-color: #c1c6c9;
      border-right: 3px solid #e3e5ea;
      border-radius: 2px; }
    .card-list .list-cards::-webkit-scrollbar-track {
      background: rgba(9, 30, 66, 0.08);
      border-right: 3px solid #e3e5ea;
      border-radius: 2px; }
    .card-list .list-cards .list-card {
      margin-bottom: 8px; }
    .card-list .list-cards .placeholder {
      width: 246px;
      border-radius: 2px;
      background: #C9D2D9; }
    .card-list .list-cards .card-form {
      padding-left: 0;
      padding-right: 0; }

